/*---------------------- Fonts --------------------------*/
@font-face {
    font-family: 'bs';
    src: url('/fonts/bs.eot');
    src: url('/fonts/bs.eot?#iefix') format('embedded-opentype'),
    url('/fonts/bs.woff') format('woff'),
    url('/fonts/bs.ttf') format('truetype'),
    url('/fonts/bs.svg#bs') format('svg');
    font-weight: normal;
    font-style: normal;
}
/*----------------- Icons Set ---------------------*/
